.accueil {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    transition: 0.5s;
}

.accueil:active {
    transform: scale(0.9);
}

.accueil:hover {
    filter: brightness(150%);
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}